
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
data () {
return {
showSea: true,
img1: require('../../assets/images/建立档案.png'),
img2: require('../../assets/images/我的客户.png'),
img3: require('../../assets/images/锅具服务.png'),
img4: require('../../assets/images/净水器.png'),
img5: require('../../assets/images/美食.png'),
img6: require('../../assets/images/营养品.png'),
img7: require('../../assets/images/纤巧壶.png'),
img8: require('../../assets/images/43件套.png'),
phoneNo: ''
}
},
methods: {
formSubmit () {
return false
},
goBack () {
this.$util.getMachine(4)
},
