tip: 0
}
})
},
toSearch () {
this.showSea = false
this.$refs.inputVal.focus()
},
toAddView () {
this.$router.push({
path: '/customerAdd',
query: {
}
})
},
toListView (tip) {
this.$router.push({
path: '/customerList',
query: {
tip: tip
}
})
}
},
created () {
},
components: {
XHeader
}
}



